:root {
  --header-image: url("hdr-img.92d32043.png");
  --header-library: url("hdr-library.2323a211.png");
  --background-color: #fff;
  --background-color-c: #000;
  --color-black: #00000f;
  --color-white: #fff;
  --text-color: #000;
  --text-color-contrast: #fff;
  --box-shadow: 0 1px 1px rgba(0, 0, 0, .12), 0 4px 4px rgba(0, 0, 0, .06), 1px 4px 6px rgba(0, 0, 0, .16);
  --background-button-d: #773204;
  --button-color: #ff6b08;
  --button-color--hvr: #816d53;
  --card-color: #f7f6f6;
  --search-color: rgba(0, 0, 0, .02);
  --loader-line: #ebe4e4;
  --header-color: #000;
  --footer-image: #f0efef;
  --film-color: #818181;
}

.darkmode {
  --background-color-c: #e5e5e5;
  --background-color: #2c2b2b;
  --color-black: #00000f;
  --color-white: #fff;
  --text-color: #fdfdfd;
  --text-color-contrast: #131212;
  --box-shadow: 0 1px 1px rgba(0, 0, 0, .12), 0 4px 4px rgba(0, 0, 0, .06), 1px 4px 6px rgba(0, 0, 0, .16);
  --background-button-d: #f0d9cc;
  --button-color: #ff6b08;
  --button-color--hvr: #886d5b;
  --card-color: #3b3a3a;
  --search-color: rgba(0, 0, 0, .137);
  --loader-line: #000;
  --footer-image: url("footer-img.400180bb.png");
  --header-color: #000;
  --film-color: #ff6b08;
}

.container-movies {
  flex-direction: column;
  display: flex;
}

.body-movies {
  margin: 60px 0 50px;
  padding: 0 3rem;
}

.cardsMovie {
  background-color: var(--background-color);
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: 2rem;
  margin: 0 auto;
  display: flex;
}

.cardsMovie__image {
  width: 17.38rem;
  height: 24.9rem;
  border-radius: 5px;
}

.movie-card {
  width: 17.38rem;
  background-color: var(--card-color);
  border-radius: 5px;
  transition: all .15s ease-in;
}

.movie-card:hover, .movie-card:focus {
  cursor: pointer;
  box-shadow: var(--box-shadow);
  transition: all .15s ease-out;
}

@media (min-width: 300px) and (max-width: 767px) {
  .movie-card {
    flex-basis: 100%;
  }

  .cardsMovie__image {
    width: 100%;
    height: 40vh;
    object-fit: cover;
    justify-content: center;
  }

  .movie__genre {
    min-width: 50vw;
    font-size-adjust: initial;
    font-size: 3vw;
    line-height: 20px;
  }

  .movie__title {
    word-spacing: 1px;
    width: auto;
    height: auto;
    font-size: 3vw;
    line-height: 25px;
  }

  .body-movies, .movielist {
    margin: 70px 10px;
    padding: 1rem;
    display: flex;
  }
}

.pagination {
  min-height: 10vh;
  justify-content: center;
  align-items: center;
  padding: 10px;
  display: flex;
}

.pagination ul {
  background: #fff;
  border-radius: 50px;
  padding: 8px;
  display: flex;
}

.pagination ul li {
  color: var(--button-color);
  text-align: center;
  cursor: pointer;
  font-size: 18px;
  font-weight: 500;
  line-height: 45px;
  list-style: none;
  transition: all .3s;
}

.pagination ul li.numb {
  height: 45px;
  width: 45px;
  border-radius: 50%;
  margin: 0 3px;
}

.pagination ul li.dots {
  cursor: default;
  font-size: 22px;
}

.pagination ul li.btn {
  padding: 0 20px;
}

.pagination ul li.prev {
  border-radius: 25px 5px 5px 25px;
}

.pagination ul li.next {
  border-radius: 5px 25px 25px 5px;
}

.pagination ul li.active, .pagination ul li.numb:hover, .pagination ul li.btn:hover {
  color: #fff;
  background: var(--button-color);
}

